import kotlinx.browser.document
import kotlinx.browser.window
import org.w3c.dom.HTMLElement
import org.w3c.dom.asList
import widgets.GamePage

object Constants {
    //val mainRectRatio = 0.8 // width / height
    //var appWidth = 300
    val dictionary = Dictionary()
    val letterLimit = 5
    val attempts = 6
    var theme = Theme.LIGHT
    val accordeonLimit = 0.6
    var seconds = 0
    var timerActive = true
    val gamePage = GamePage()
    val popupDelay = "1s"
    val popupRoll = "2s"
    var nsfwFilterOn = true
    var gameLoaded = false


    fun updateTheme(newTheme: Theme= theme){
        this.theme = newTheme
        castColourToId("background", bgColour = theme.outsideColour)
        castColourToId("main", textColour = theme.textColour, bgColour = theme.backgroundColour)
        document.getElementById("triangle").also { if(it is HTMLElement) it.style.borderLeftColor = theme.textColour}
        document.getElementsByClassName("buttonClass").asList().forEach {
            if(it is HTMLElement){
                it.style.backgroundColor = when(it.style.getPropertyValue("--state")){
                    "green" -> theme.greenColour
                    "yellow" -> theme.yellowColour
                    "gray" -> theme.grayColour
                    else -> theme.buttonColour
                }
            }
        }
        onUpdateTheme()
    }

    var onUpdateTheme = {}


    fun castColourToId(id: String, textColour: String? = null, bgColour: String?=null){
        if(bgColour!=null){
            document.getElementById(id).also { if(it is HTMLElement) it.style.backgroundColor = bgColour}
        }
        if(textColour!=null){
            document.getElementById(id).also { if(it is HTMLElement) it.style.color = textColour }
        }

    }


    enum class Theme{
        LIGHT {
            override val textColour: String
                get() = "#110A01"
            override val backgroundColour: String
                get() =  "#EDEAEA"
            override val outsideColour: String
                get() = "#EDEAEA"
            override val buttonColour: String
                get() = "#ECEAEF"
            override val greenColour: String
                get() = "#6CFF40"
            override val yellowColour: String
                get() = "#F8ED45"
            override val grayColour: String
                get() = "#535353"
        },
        DARK {
            override val textColour: String
                get() = "#ECEAEF"
            override val backgroundColour: String
                get() = "#1F1B16"
            override val outsideColour: String
                get() = "#1F1B16"
            override val buttonColour: String
                get() = "#262320"
            override val greenColour: String
                get() = "#23991D"
            override val yellowColour: String
                get() = "#A88F1E"
            override val grayColour: String
                get() = "#535353"
        },
        PUNGO {
            override val textColour: String
                get() = "#F9E178"
            override val backgroundColour: String
                get() = "#1A1321"
            override val outsideColour: String
                get() = "#1A1321"
            override val buttonColour: String
                get() = "#411F5A"
            override val greenColour: String
                get() = "#1D8C70"
            override val yellowColour: String
                get() = "#D7522D"
            override val grayColour: String
                get() = "#535353"
        };

        abstract val textColour: String
        abstract val backgroundColour: String
        abstract val outsideColour: String
        abstract val buttonColour: String
        abstract val greenColour: String
        abstract val yellowColour: String
        abstract val grayColour: String
    }


}